@font-face {
  font-family: "NotoSans";
  font-weight: 400;
  src: url("../../fonts/notokr-regular.eot");
  src: url("../../fonts/notokr-regular.eot?#iefix") format("embedded-opentype"), url("../../fonts/notokr-regular.woff") format("woff"), url("../../fonts/notokr-regular.woff2") format("woff2");
}
@font-face {
  font-family: "NotoSans";
  font-weight: 500;
  src: url("../../fonts/notokr-medium.eot");
  src: url("../../fonts/notokr-medium.eot?#iefix") format("embedded-opentype"), url("../../fonts/notokr-medium.woff") format("woff"), url("../../fonts/notokr-medium.woff2") format("woff2");
}
@font-face {
  font-family: "NotoSans";
  font-weight: 700;
  src: url("../../fonts/notokr-bold.eot");
  src: url("../../fonts/notokr-bold.eot?#iefix") format("embedded-opentype"), url("../../fonts/notokr-bold.woff") format("woff"), url("../../fonts/notokr-bold.woff2") format("woff2");
}
body {
  font-family: "NotoSans";
}

.ui-test-btn {
  display: inline-block;
  vertical-align: top;
  border: 0 none;
  background: #333;
  padding: 10px 20px;
  font-size: 16px;
  color: #fff;
  border-radius: 10px;
}
.ui-test-btn > span {
  display: inline-block;
}
.ui-test-btn:hover, .ui-test-btn:focus {
  color: #fff;
}

.code_in {
  padding-bottom: 200px;
}

/* common */
.ctr-btn {
  margin: 10px 0 20px;
}
.ctr-btn .decs {
  display: inline-block;
  margin-top: 6px;
  margin-left: 20px;
}
.ctr-btn .style-toggle-btn {
  padding: 10px;
  border: 0 none;
  background: #837466;
  color: #fff;
  font-weight: bold;
  border-radius: 6px;
  margin: 0;
  outline: none;
}
.ctr-btn .style-toggle-btn.ui-active {
  background: #d1c3b6;
  color: #333;
}

[kui-tab] {
  border: 1px solid red;
}
[kui-tab] .kui-tab-nav {
  font-size: 0;
}
[kui-tab] .kui-tab-nav li {
  display: inline-block;
}
[kui-tab] .kui-tab-nav li.kui-active a {
  background-color: #fff;
  color: #000;
}
[kui-tab] .kui-tab-nav li a {
  display: block;
  width: 100px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  background: #ccc;
  font-size: 16px;
  color: #666;
}
[kui-tab] .kui-tab-contents {
  padding: 30px;
  border: 1px solid #000;
}
[kui-tab] .kui-tab-contents > * {
  display: none;
}
[kui-tab] .kui-tab-contents > *.kui-active {
  display: block;
}

.custom-style {
  /* Sticky Custom Style*/
  /* Dropdown */
  /* cascading grid */
  /* Collapse */
  /* custom toggle */
  /* tab */
  /* scroll tab */
  /* selectbox */
  /* modal */
  /* scrollspy */
}
.custom-style a {
  text-decoration: none;
}
.custom-style [data-modules-sticky] .sticky-inner {
  display: inline-block;
  background: #333;
  color: #fff;
  position: relative;
  padding: 10px;
  border-radius: 10px;
}
.custom-style [data-modules-sticky].fixed {
  position: fixed;
}
.custom-style #break {
  border: 1px solid #aaa;
  border-radius: 4px;
  position: relative;
}
.custom-style #break5 {
  position: absolute;
  top: 800px;
  left: 0;
  right: 0;
  border-top: 1px solid red;
}
.custom-style .ui-dropdown {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 10px;
  color: #333;
  text-align: left;
}
.custom-style .ui-dropdown .dropdown-menu {
  right: 0;
  border: 1px solid #ccc;
  padding: 10px;
}
.custom-style .ui-dropdown .dropdown-menu a {
  display: block;
  padding: 5px 0;
}
.custom-style .ui-dropdown a.readonly, .custom-style .ui-dropdown a.disabled {
  cursor: default;
  color: #ccc;
}
.custom-style .ui-dropdown a.readonly:hover, .custom-style .ui-dropdown a.disabled:hover {
  text-decoration: none;
}
.custom-style [data-items] {
  padding: 20px;
  background: #b2cef5;
  color: #333;
}
.custom-style [data-modules-collapse] {
  border: 1px solid #999;
  display: block;
  width: 600px;
  padding: 10px 20px;
  position: relative;
  font-size: 16px;
  color: #333;
}
.custom-style [data-modules-collapse]:after {
  content: "";
  width: 10px;
  height: 10px;
  border-right: 2px solid #333;
  border-bottom: 2px solid #333;
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -80%) rotate(45deg);
}
.custom-style [data-modules-collapse].selected:after {
  transform: translate(0, -30%) rotate(-135deg);
}
.custom-style .accordion-panel {
  border: 1px solid #999;
  border-top: 0 none;
  width: 600px;
  margin-top: -1px;
}
.custom-style .accordion-panel.collapsani {
  position: relative;
  transition: height 0.3s;
  height: 0;
  overflow: hidden;
}
.custom-style .accordion-panel .inset {
  padding: 10px 20px;
  min-height: 100px;
}
.custom-style .acc-parent {
  border-top: 1px solid #999;
  width: 600px;
}
.custom-style .acc-parent [data-modules-collapse] {
  border-top: 0 none;
}
.custom-style [data-modules-customtoggle] {
  display: inline-block;
  padding: 10px 20px;
  background: #333;
  border: 0 none;
  color: #fff;
  border: 1px solid transparent;
}
.custom-style [data-modules-customtoggle].ui-active {
  color: #333;
  background: #fff;
  border: 1px solid #999;
}
.custom-style .tab-content > .tab-pane.ui-active {
  display: block;
}
.custom-style .tab-content > .tab-pane {
  display: none;
}
.custom-style .ui-tab {
  border-bottom: 1px solid #333;
  border-left: 1px solid #333;
  font-size: 0;
}
.custom-style .ui-tab [role=presentation] {
  display: inline-flex;
  font-size: 16px;
  border-top: 1px solid #333;
  border-right: 1px solid #333;
}
.custom-style .ui-tab [role=presentation] a {
  display: block;
  padding: 5px 20px;
  color: #999;
  background: #e6e6e6;
}
.custom-style .ui-tab [role=presentation].ui-active a {
  text-decoration: underline;
  font-weight: 700;
  color: #333;
  background: #fff;
}
.custom-style .tab-content {
  border: 1px solid #333;
}
.custom-style .tab-content .tab-pane {
  padding: 20px;
  min-height: 300px;
}
.custom-style .scroll-tab {
  margin: 0 40px;
  width: 473px !important;
}
.custom-style .scroll-tab .tab-control {
  display: block;
  position: relative;
  width: 50px;
  height: 35px;
  position: absolute;
  top: 0;
  background: transparent;
  overflow: hidden;
  border: 0 none;
  outline: none;
}
.custom-style .scroll-tab .tab-control.prev-tab {
  left: -50px;
}
.custom-style .scroll-tab .tab-control.next-tab {
  right: -50px;
}
.custom-style .selectwrap {
  margin-top: 20px;
}
.custom-style .custom-selectbox {
  width: 500px;
}
.custom-style .custom-selectbox .ui-label a {
  display: block;
  border: 1px solid #333;
  line-height: 43px;
  height: 43px;
  padding: 0 0 0 20px;
  position: relative;
  color: #333;
  font-weight: 700;
}
.custom-style .custom-selectbox .ui-label a:after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  transform: rotate(135deg);
  border-top: 1px solid #000;
  border-right: 1px solid #000;
  position: absolute;
  top: 50%;
  right: 20px;
  margin-top: -10px;
}
.custom-style .custom-selectbox.ui-active .ui-label a:after {
  transform: rotate(-45deg);
  margin-top: -4px;
}
.custom-style .custom-selectbox.ui-bottom .ui-selectbox {
  top: auto;
  bottom: 100%;
  border-top: 1px solid #000;
  border-bottom: 0 none;
}
.custom-style .custom-selectbox .ui-selectbox {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  z-index: 99;
  background: #fff;
  border: 1px solid #000;
  border-top: 0 none;
}
.custom-style .custom-selectbox .ui-selectbox a {
  display: block;
  height: 43px;
  line-height: 43px;
  padding-left: 20px;
}
.custom-style .custom-selectbox.disabled, .custom-style .custom-selectbox.readonly {
  opacity: 0.5;
}
.custom-style .custom-selectbox.disabled a, .custom-style .custom-selectbox.readonly a {
  cursor: default;
  font-weight: 400;
}
.custom-style .ui-modal .ui-modal-dialog {
  border-radius: 10px;
  padding: 20px;
}
.custom-style .ui-modal .ui-modal-dialog.custom-msg {
  width: 300px;
  height: auto;
}
.custom-style .ui-modal .ui-modal-dialog.custom-msg .modal-body {
  text-align: center;
  padding: 20px 0;
}
.custom-style .ui-modal .ui-modal-dialog.custom-msg .modal-footer {
  text-align: center;
}
.custom-style .ui-modal .ui-modal-dialog.custom-msg .modal-footer button {
  background: #333;
  border: 0 none;
  padding: 10px;
  min-width: 60px;
  color: #fff;
  border-radius: 10px;
}
.custom-style .ui-modal .ui-modal-dialog.custom-msg .modal-footer button ~ button {
  margin-left: 10px;
}
.custom-style .ui-modal .close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  width: 40px;
  height: 40px;
  border: 0 none;
  text-indent: -9999em;
}
.custom-style .ui-modal .close:before, .custom-style .ui-modal .close:after {
  content: "";
  display: block;
  transform: translate(-50%, -50%) rotate(-45deg);
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  height: 1px;
  background: #333;
}
.custom-style .ui-modal .close:after {
  transform: translate(-50%, -50%) rotate(45deg);
}
.custom-style .ui-scrollspy .fixed .inner {
  left: 20px;
  right: 20px;
}
.custom-style .ui-scrollspy .ui-spynav {
  position: relative;
  background: #fff;
}
.custom-style .ui-scrollspy .ui-spynav:after {
  content: "";
  display: block;
  overflow: hidden;
  width: 100%;
  height: 1px;
  background: #333;
  position: absolute;
  bottom: -1px;
  position: absolute;
}
.custom-style .ui-scrollspy .ui-spynav li a {
  color: #333;
  background: #f7f7f7;
  display: block;
  padding: 20px;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
}
.custom-style .ui-scrollspy .ui-spynav li.ui-active a {
  background: #333;
  color: #fff;
  text-decoration: underline;
}
.custom-style .ui-scrollspy .pages {
  line-height: 800px;
  font-size: 200px;
  text-align: center;
}